import React from 'react';

let ProductExcerpt = React.createClass({displayName: "ProductExcerpt",
  render() {
    let product = this.props.product;

    let description;
    if (product.description.size > 300) {
      let excerpt = product.description.slice(0,400);
      description = (
        React.createElement("div", null, 
          excerpt, 
          React.createElement("a", {href: "#description", className: "ds-product-read-description"}, "Leia mais")
        )
      );
    } else {
      description = product.description;
    }

    return (
      React.createElement("div", {className: "ds-product-excerpt"}, 
        React.createElement("h4", {className: "ds-product-excerpt-title"}, "Descrio"), 
        description 
      )
    );
  }
});

export default ProductExcerpt;



