import React from 'react';
import kebabCase from 'lodash/string/kebabCase';

let ProductTitle = React.createClass({displayName: "ProductTitle",
  render() {
    let product = this.props.product;
    let brandURL = '/' + kebabCase(product.brand.slug) + '/b';

    return (
      React.createElement("div", {className: "ds-product-title"}, 
        React.createElement("h1", {className: "ds-product-name"}, product.name), 
        React.createElement("div", {className: "ds-brand"}, 
          "por ", React.createElement("a", {title: product.brand.name, href: brandURL}, product.brand.name)
        )
      )
    );
  }
});

export default ProductTitle;



