import React from 'react';
import Img from '../Img';
import { FormattedNumber } from 'react-intl';

let ProductSkuSelector = React.createClass({displayName: "ProductSkuSelector",
  render() {
    let product = this.props.product;

    let skus = product.skus.map((sku) => {

      let price;
      if (sku.offers[0].availability > 0) {
        let listPrice;
        if (sku.offers[0].listPrice > sku.offers[0].price) {
          listPrice = React.createElement("s", null, "De: ", React.createElement(FormattedNumber, {style: "currency", value: sku.offers[0].listPrice, currency: this.props.currency}));
        }

        price = (
          React.createElement("div", null, 
            listPrice, 
            React.createElement("div", null, "Por: ", React.createElement(FormattedNumber, {style: "currency", value: sku.offers[0].price, currency: this.props.currency}), "  vista")
          )
        );
      }

      return (
        React.createElement("div", {className: "row", key: sku.id}, 
          React.createElement("div", {className: "col-md-2"}, 
            React.createElement(Img, {className: "thumbnail", 
              src: sku.images[0].src, 
              accountName: this.props.accountName, 
              alt: "", title: "", 
              height: "65", width: "65"})
          ), 
          React.createElement("div", {className: "col-md-7"}, 
            React.createElement("b", null,  sku.name), 
            price 
          )
        )
      );
    });

    return (
      React.createElement("div", null, 
        skus 
      )
    );
  }
});

export default ProductSkuSelector;



