import React from 'react';

let ProductStock = React.createClass({displayName: "ProductStock",
  render() {
    let sku = this.props.product.items[0];

    let stock;
    if (sku.sellers[0].offer.availableQuantity > 0) {
      if (sku.sellers[0].offer.availableQuantity < 6) {
        stock = React.createElement("span", {className: "ds-product-in-stock"}, "Apenas ",  sku.sellers[0].offer.availableQuantity, " em estoque.");
      } else {
        stock = React.createElement("span", {className: "ds-product-in-stock"}, "Em Estoque.");
      }
    } else {
      stock = React.createElement("span", {className: "ds-product-no-stock"}, "Sem Estoque.");
    }

    return (
      React.createElement("div", {className: "ds-product-stock"}, 
        stock 
      )
    );
  }
});

export default ProductStock;



