import React from 'react';
import { Link } from 'react-router';
import Style from './header.less';
import { Navbar, Nav, DropdownButton, MenuItem } from 'react-bootstrap';

let Header = React.createClass({displayName: "Header",
  render() {
    return (
      React.createElement("div", null, 
        React.createElement(Navbar, {brand: "Comuna"}, 
          React.createElement(Nav, null, 
            React.createElement(DropdownButton, {title: "Menu"}, 
              React.createElement(Link, {to: "home"}, 
                React.createElement(MenuItem, null, "Home")
              ), 
              React.createElement(Link, {to: "comidas"}, 
                React.createElement(MenuItem, null, "Comidas")
              ), 
              React.createElement(Link, {to: "bebidas"}, 
                React.createElement(MenuItem, null, "Bebidas")
              )
            )
          )
        )
      )
    );
  }
});

export default Header;



