import React from 'react';

let ProductAddCart = React.createClass({displayName: "ProductAddCart",
  handleAddToCart() {
    let product = this.props.product;
    let sku = product.skus[0];
    let qty = React.findDOMNode(this.refs.quantity).value;
    this.props.addToCart(sku.id, qty, 1);
  },

  render() {
    let product = this.props.product;
    let sku = product.skus[0];
    let addToCart;
    let loading;
    if (sku.offers[0].availability > 0) {
      let isAvailable;
      if (sku.offers[0].availability > 1) {
        let maxQuantity = [];
        let j = sku.offers[0].availability;
        for (var i = 1; i <= 30 && i < j; i++) {
          maxQuantity.push(
            React.createElement("option", {key: i}, i )
          );
        }
        isAvailable = (
          React.createElement("form", {className: "ds-product-quantity"}, 
            React.createElement("label", null, 
              "Quant:", 
              React.createElement("select", {ref: "quantity"}, 
                maxQuantity
              )
            )
          )
        );
      }
      addToCart = (
        React.createElement("div", {className: "ds-product-add-cart"}, 
          isAvailable, 

          React.createElement("a", {onClick: this.handleAddToCart, className: "ds-product-add-cart-btn btn btn-block btn-success"}, 
            React.createElement("i", {className: "fa fa-shopping-cart"}), " Adicionar ao carrinho"
          )
        )
      );

      loading = (
        React.createElement("div", {className: "ds-product-add-cart"}, 
          isAvailable, 

          React.createElement("a", {className: "ds-product-add-cart-btn btn btn-block btn-success"}, 
            React.createElement("i", {className: "fa fa-shopping-cart"}), " ..."
          )
        )
      );
    }

    return (
      React.createElement("div", null, 
        
          this.props.cartLoading ?
          loading :
          addToCart
        
      )
    );
  }
});

export default ProductAddCart;



