import React from 'react';

import Img from '../Img';
import { Link } from 'react-router';
import { FormattedNumber } from 'react-intl';
import shelfItemCSS from './shelfItem.less';
import { Button } from 'react-bootstrap';

let ShelfItem = React.createClass({displayName: "ShelfItem",

  addToCart() {
    let orderForm = this.props.orderForm;
    let sampleProduct = this.props.product.skus[0];
    let item = {
      'id': sampleProduct.id,
      quantity: 1,
      'seller': sampleProduct.offers[0].seller.id
    };

    storefront.flux.actions.CartActions.addToCart(orderForm.orderFormId, [item]);
  },

  render() {
    let product = this.props.product || '';
    let name = product.name;
    let slug = product.slug;
    let brand = product.brand.name;
    let sampleProduct = product.skus[0];
    let price = sampleProduct.offers[0].price;
    let imageSrc = sampleProduct.images[0].src;

    return (
      React.createElement("div", {className: "ds-shelf-item"}, 
        React.createElement("div", {className: "full-length ds-shelf-item-image-container"}, 
          React.createElement(Img, {className: "full-length ds-shelf-item-image", 
            src: imageSrc, 
            accountName: this.props.accountName, 
            alt: "", title: ""})
        ), 
        React.createElement("div", {className: "full-length ds-product-info container"}, 
          React.createElement("h3", {className: "ds-shelf-item-name", title: name }, 
            name 
          ), 
          React.createElement("p", null,  product.description), 
          React.createElement("div", {className: "row ds-product-bottom"}, 
            React.createElement("h4", {className: "col-xs-6 ds-shelf-item-price"}, 
              React.createElement(FormattedNumber, {style: "currency", value: price, currency: this.props.currency})
            ), 
            React.createElement("div", {className: "col-xs-6 ds-add-to-cart"}, 
              React.createElement(Button, {onClick: this.addToCart}, "Pedir")
            )
          )
        )
      )
    );
  }
});

export default ShelfItem;



