import React from 'react';

let ProductDescription = React.createClass({displayName: "ProductDescription",
  render() {
    let product = this.props.product;
    return (
      React.createElement("div", {className: "ds-product-description row"}, 
        React.createElement("h4", {id: "description", className: "ds-product-description-title col-md-12"}, "Descrio do produto"), 
        React.createElement("div", {className: "ds-product-description-value col-md-12"}, 
           product.description
        )
      )
    );
  }
});

export default ProductDescription;



