import React, { PropTypes } from 'react';
import connectToStores from 'utils/connectToStores.js';

import ProductTitle from 'components/product/Title';
import ProductPrice from 'components/product/Price';
import ProductStock from 'components/product/Stock';
import ProductSeller from 'components/product/Seller';
import ProductExcerpt from 'components/product/Excerpt';
import ProductSkuSelector from 'components/product/SkuSelector';
import ProductShare from 'components/product/Share';
import ProductAddCart from 'components/product/AddCart';
import ProductDescription from 'components/product/Description';
import ProductSpecification from 'components/product/Specification';
import Img from 'components/Img';
import { PureRenderMixin } from 'react/lib/ReactComponentWithPureRenderMixin';

let Product = React.createClass({displayName: "Product",
  mixins: [ PureRenderMixin ],
  statics: {
    getStores() {
      return [
        storefront.flux.stores.ProductStore,
        storefront.flux.stores.ShopStore,
        storefront.flux.stores.CartStore
      ];
    }
  },

  getProduct() {
    const productSlug = this.props.params.product;
    return this.props.ProductStore.get(productSlug);
  },

  addToCart(id, qty, seller) {
    let orderForm = this.props.CartStore.get('orderForm');
    let item = {
      'id': id,
      quantity: qty,
      'seller': seller
    };

    storefront.flux.actions.CartActions.addToCart(orderForm.orderFormId, [item]);
  },

  componentDidMount() {
    const product = this.getProduct();

    // TODO look for details that are not on the search results
    if (!product) {
      storefront.flux.actions.ProductActions.requestProduct({
        accountName: this.props.ShopStore.get('accountName'),
        product: this.props.params.product
      });
    }
  },

  render() {
    const product = this.getProduct();
    const ShopStore = this.props.ShopStore;
    const CartStore = this.props.CartStore;

    if (!product) {
      return (
        React.createElement("div", null, "loading...")
      );
    }

    const sku = product.skus[0];
    return (
      React.createElement("div", {className: "ds-main container-fluid"}, 

        React.createElement("div", {className: "ds-product-info row"}, 
          React.createElement("div", {className: "ds-product-image-container col-md-4"}, 
            React.createElement(Img, {className: "ds-product-image img-responsive", 
              src:  sku.images[0].src, 
              accountName:  ShopStore.get('accountName'), 
              alt:  product.name, title:  product.name, 
              height: "490", width: "490"})
          ), 
          React.createElement("div", {className: "ds-product-main-info col-md-6"}, 
            React.createElement(ProductTitle, {product: product}), 

             product.skus.size == 1 ?
              React.createElement("div", null, 
                React.createElement(ProductPrice, {product: product, currency: ShopStore.get('currency')}), 
                React.createElement(ProductStock, {product: product}), 
                React.createElement(ProductSeller, {product: product}), 

                React.createElement(ProductExcerpt, {product: product})
              )
              :
              React.createElement(ProductSkuSelector, {
                product: product, 
                currency: ShopStore.get('currency'), 
                accountName: ShopStore.get('accountName')})
            
          ), 
          React.createElement("div", {className: "ds-product-sidebar col-md-2"}, 
            React.createElement("div", {className: "row"}, 
              React.createElement(ProductShare, {product: product})
            ), 
            React.createElement("div", {className: "row"}, 
              React.createElement(ProductAddCart, {product: product, addToCart: this.addToCart, 
                              cartLoading: CartStore.get('addLoading')})
            )
          )
        ), 

        React.createElement(ProductDescription, {product: product}), 

        React.createElement(ProductSpecification, {product: product})
      )
    );
  }
});

export default connectToStores(Product);



