import React from 'react';

let ProductShare = React.createClass({displayName: "ProductShare",
  render() {
    let product = this.props.product;
    return (
      React.createElement("div", {className: "ds-product-social-share"}, 
        React.createElement("a", {className: "ds-product-social-share-email", href: "#", title: "Compartilhar por e-mail"}, "Compartilhar"), "", 
        React.createElement("a", {className: "ds-product-social-share-email-icon", href: "#", title: "Compartilhar por e-mail"}, React.createElement("i", {className: "fa fa-envelope-o"})), "", 
        React.createElement("a", {className: "ds-product-social-share-facebook", href: "#", title: "Compartilhar no Facebook"}, React.createElement("i", {className: "fa fa-facebook-official"})), "", 
        React.createElement("a", {className: "ds-product-social-share-twitter", href: "#", title: "Compartilhar no Twitter"}, React.createElement("i", {className: "fa fa-twitter"}))
      )
    );
  }
});

export default ProductShare;



