import React from 'react';
import { FormattedNumber } from 'react-intl';

let ProductPrice = React.createClass({displayName: "ProductPrice",
  render() {
    let sku = this.props.product.items[0];
    let hasListPrice = sku.sellers[0].offer.listPrice > sku.sellers[0].offer.price;
    let discountValue = sku.sellers[0].offer.listPrice - sku.sellers[0].offer.price;
    let discountPercent = discount * 100 / sku.sellers[0].offer.listPrice;
    discountPercent = discountPercent.split('.')[0];

    let listPrice, discount;
    if (hasListPrice) {
      listPrice = (
        React.createElement("tr", {className: "ds-product-list-price-row", key: "listPrice"}, 
          React.createElement("td", {className: "ds-product-price-table-label"}, 
            "Preo de Tabela:"
          ), 
          React.createElement("td", {className: "ds-product-price-table-value"}, 
            React.createElement(FormattedNumber, {style: "currency", value: sku.sellers[0].offer.listPrice, currency: this.props.currency})
          )
        )
      );
      discount = (
        React.createElement("tr", {className: "ds-product-savings-row", key: "discount"}, 
          React.createElement("td", {className: "ds-product-price-table-label"}, 
            "Economia de:"
          ), 
          React.createElement("td", {className: "ds-product-price-table-value"}, 
            React.createElement(FormattedNumber, {style: "currency", value: discount, currency: this.props.currency}), " (", discountPercent, ")"
          )
        )
      );
    } else {
      listPrice = '';
      discount = '';
    }

    return (
      React.createElement("div", {className: "ds-product-price-container"}, 
        React.createElement("table", {className: "ds-product-price-table"}, 
          React.createElement("tbody", null, 
            listPrice, 
            React.createElement("tr", {className: "ds-product-price-row", key: "price"}, 
              React.createElement("td", {className: "ds-product-price-table-label"}, 
                "Preo:"
              ), 
              React.createElement("td", {className: "ds-product-price-table-value"}, 
                 sku.sellers[0].offer.price
              )
            ), 
            discount 
          )
        )
      )
    );
  }
});

export default ProductPrice;



