import React from 'react';

let ProductSpecification = React.createClass({displayName: "ProductSpecification",
  render() {
    let product = this.props.product;

    let spec = product.properties.map((prop) =>
      React.createElement("tr", {key:  prop.facet.name}, 
        React.createElement("th", null,  prop.facet.name), 
        React.createElement("td", null,  prop.facet.values[0].replace('\r\n', '<br/>') )
      )
    );
    return (
      React.createElement("div", {className: "ds-product-specification row"}, 
        React.createElement("h4", {id: "specification", className: "ds-product-specification-title col-md-12"}, "Especificaes"), 
        React.createElement("div", {className: "ds-product-specification-value col-md-12"}, 
          React.createElement("table", {className: "ds-product-specification-table table table-striped"}, 
            React.createElement("tbody", null, 
            spec 
            )
          )
        )
      )
    );
  }
});

export default ProductSpecification;



