import React from 'react';
import connectToStores from 'utils/connectToStores.js';
import { Navigation, State, Link } from 'react-router';
import Style from './footer.less';
import { FormattedNumber } from 'react-intl';
import { Button } from 'react-bootstrap';

let Footer = React.createClass({displayName: "Footer",
  mixins: [ Navigation, State ],
  statics: {
    getStores() {
      return [
        storefront.flux.stores.CartStore,
        storefront.flux.stores.ShopStore
      ];
    }
  },

  render() {
    let currency = this.props.ShopStore.get('currency');
    let orderForm = this.props.CartStore.get('orderForm');
    let valueComponent, value;
    if (currency && orderForm && orderForm.value) {
      value = orderForm.value / 100;
      valueComponent = React.createElement(FormattedNumber, {style: "currency", value: value, currency: currency});
    }

    if (this.isActive('checkout')) {
      return (
        React.createElement("div", {className: "ds-footer"}, 
          React.createElement("span", {className: "ds-value"}, valueComponent ), 
          React.createElement(Link, {to: "success", className: "pull-right"}, 
            React.createElement(Button, null, "Confirmar")
          )
        )
      );
    }

    if (this.isActive('success')) {
      return null;
    }

    return (
      React.createElement("div", {className: "ds-footer"}, 
        React.createElement("span", {className: "ds-value"}, valueComponent ), 
        React.createElement(Link, {to: "checkout", className: "pull-right"}, 
          React.createElement(Button, null, "Pagar")
        )
      )
    );
  }

});

export default connectToStores(Footer);



